  //******************************************************************************************** */
  getPayslip(id: number) {
    this.loading = true;
    this.reportAPI.payslip(id).subscribe({
      next: (value) => {
        this.loading = false;
        this.notificationAPI.alertSuccess("Downloading Payslip Report");
        let url = window.URL.createObjectURL(value.data);
        let a = document.createElement("a");
        document.body.appendChild(a);
        a.setAttribute("style", "display: none");
        a.setAttribute("target", "blank");
        a.href = url;
        a.download = value.filename;
        a.click();
        window.URL.revokeObjectURL(url);
        a.remove();
      },
      error: (err) => {
        this.loading = false;
        this.notificationAPI.alertWarning("NO PAYSLIP REPORT FOUND");
      },
      complete: () => {},
    });
  }

  download(row) {
    Swal.fire({
      title: "Generate Employee P9 Form",
      html: `
        <input id="swal-input1" class="swal2-input" type="number" placeholder="Enter Year">
      `,
      showCancelButton: true,
      confirmButtonText: "Submit",
      showLoaderOnConfirm: true,
      icon: "info",
      preConfirm: () => {
        const year = (<HTMLInputElement>document.getElementById("swal-input1"))
          .value;
        if (!year) {
          Swal.showValidationMessage("Year is required");
        }
        return year;
      },
    }).then((result) => {
      if (result.isConfirmed) {
        this.downloadP9(row, result.value);
      }
    });
  }

  downloadP9(row, year) {
    this.loading = true;
    let params = new HttpParams().set("period_y", year.toString()).set("empPF", row.empNo.toString());
    this.reportAPI.generateEmployeeP9form(params).subscribe(
      (res) => {
        this.loading = false;
        if (res.statusCode === 404) {
          this.notificationAPI.alertWarning(res.message);
        } else {
          this.notificationAPI.alertSuccess("Downloading Employee P9-FORM");
          let url = window.URL.createObjectURL(res.data);
          let a = document.createElement("a");
          document.body.appendChild(a);
          a.setAttribute("style", "display: none");
          a.setAttribute("target", "blank");
          a.href = url;
          a.download = res.filename;
          a.click();
          window.URL.revokeObjectURL(url);
          a.remove();
        }
      },
      (err) => {
        this.loading = false;
        this.notificationAPI.alertWarning("NO DATA FOUND");
      }
    );
  }

  
