  private saveToFileSystem(
    filename: any,
    response: any,
    year,
    month,
    extension
  ) {
    const blob = new Blob([response], { type: "application/vnd.ms-excel" });
    const url = window.URL.createObjectURL(blob);
    const anchor = document.createElement("a");
    anchor.download = filename + "_for_" + year + "_" + month + "." + extension;
    anchor.href = url;
    anchor.click();
  }

  getPayslip(id: number) {
    this.loading = true;
    this.reportAPI.payslip(id).subscribe({
      next: (value) => {
        this.loading = false;
        this.notificationAPI.alertSuccess("Downloading Payslip Report");
        let url = window.URL.createObjectURL(value.data);
        let a = document.createElement("a");
        document.body.appendChild(a);
        a.setAttribute("style", "display: none");
        a.setAttribute("target", "blank");
        a.href = url;
        a.download = value.filename;
        a.click();
        window.URL.revokeObjectURL(url);
        a.remove();
      },
      error: (err) => {
        this.loading = false;
        this.notificationAPI.alertWarning("NO PAYSLIP REPORT FOUND");
      },
      complete: () => {},
    });
  }
