import { HttpParams } from "@angular/common/http";
import { Component, OnInit, ViewChild } from "@angular/core";
import { FormGroup, FormBuilder } from "@angular/forms";
import { MatDialog, MatDialogConfig } from "@angular/material/dialog";
import { MatPaginator } from "@angular/material/paginator";
import { MatSort } from "@angular/material/sort";
import { MatTableDataSource } from "@angular/material/table";
import { Router, ActivatedRoute } from "@angular/router";
import { Subject, takeUntil, Subscription } from "rxjs";
import { TokenStorageService } from "src/app/core/service/token-storage.service";
import { SalariesService } from "src/app/erp-finance/data/services/salaries.service";
import { AccountingJournalTransactionsComponent } from "src/app/erp-finance/modules/finance-management/transactions/dialogs/accounting-journal-transactions/accounting-journal-transactions.component";
import { PayrollService } from "src/app/erp-hr/data/payroll-services/payroll.service";
import { AccessControlService } from "src/app/erp-procurement/data/services/_AccessControlService.service";
import { NotificationService } from "src/app/erp-procurement/data/services/notification.service";
import { SnackbarService } from "src/app/shared/services/snackbar.service";
import Swal from "sweetalert2";

@Component({
  selector: "app-manage-to-finance",
  templateUrl: "./manage-to-finance.component.html",
  styleUrls: ["./manage-to-finance.component.sass"],
})
export class ManageToFinanceComponent implements OnInit {
  displayedColumns: string[] = [
    "employeeName",
    "employeeAc",
    "empIdno",
    "empGrosspay",
    "empPaye",
    "empNhif",
    "empNssf",
    "empHouselevy",
    "empPension",
    "empGratuity",
    "empStaffloan",
    "empNetpay",
    "action",
  ];

  //   {
  //     "payrollCircleCode": "dc8Nr20240527",
  //     "openingDate": "2024-05-27T14:22:27.855483",
  //     "activeEmployees": 0,
  //     "totalOpeningGrossPay": 0,
  //     "totalOpeningDeductions": 0,
  //     "totalOpeningAllowances": 0,
  //     "periodY": 2024,
  //     "periodM": "May",
  //     "closingDate": "2024-05-27T15:39:10.46625",
  //     "closurePaidEmployees": 2,
  //     "totalClosingGrossPay": 180000,
  //     "totalCLosingDeductions": 51746.7,
  //     "totalCLosingAllowances": 0,
  //     "isPayslipOpen": "Y",
  //     "payslipOpenDate": "2024-05-27T14:35:43.81308",
  //     "payslipOpenBy": "KBUser",
  //     "openBy": "KBUser",
  //     "closedBy": "KBUser",
  //     "status": "CLOSED"
  // }
  dataSource = new MatTableDataSource<any>([]);
  @ViewChild(MatPaginator) paginator!: MatPaginator;
  @ViewChild(MatSort) sort!: MatSort;

  // define the needed variables
  showForm = false;
  pageFunction = "Add";
  mngForm!: FormGroup;
  currentUser: any;
  formData: any;
  destroy$: Subject<boolean> = new Subject<boolean>();

  downloadLoading: boolean = false;
  id: any;
  hideSubmit = false;
  showApprovals = false;

  // ****************************************************************************************************************************
  constructor(
    private fb: FormBuilder,
    private router: Router,
    private activatedRoute: ActivatedRoute,
    private tokenStorageService: TokenStorageService,
    private snackbar: SnackbarService,
    private route: ActivatedRoute,
    private notificationAPI: NotificationService,
    private dialog: MatDialog,
    private accessControlService: AccessControlService,
    private payrollService: PayrollService
  ) {
    this.currentUser = this.tokenStorageService.getUser().username;
  }

  // ****************************************************************************************************************************
  //define actions on component initiaalization
  ngOnInit(): void {
    this.showForm = true;

    this.route.queryParams.subscribe((params) => {
      console.log("res", params);

      if (params.hasOwnProperty("action")) {
        const action = params["action"];
        this.pageFunction = action;
        // Parse the JSON string back into an object
        this.formData = params["record"] ? JSON.parse(params["record"]) : null;
      }
    });

    if (this.pageFunction === "View Payroll Record") {
      this.showApprovals = false;
      this.displayedColumns = [
        "employeeName",
        "employeeAc",
        "empIdno",
        "empGrosspay",
        "empPaye",
        "empNhif",
        "empNssf",
        "empHouselevy",
        "empPension",
        "empGratuity",
        "empStaffloan",
        "empNetpay",
        "action",
      ];

      this.showPayrollDetails();
    }

    if (this.pageFunction === "Push Payroll To Finance") {
      this.showApprovals = true;
      this.displayedColumns = [
        "payrollCircleCode",
        "period",
        "openingDate",
        "closingDate",
        "activeEmployees",
        "closurePaidEmployees",
        "totalOpeningGrossPay",
        "totalClosingGrossPay",
        "totalOpeningDeductions",
        "totalClosingDeductions",
        "totalOpeningAllowances",
        "totalClosingAllowances",
        "status",
      ];

      this.getDataByYearAndMonth();
    }
  }

  // ****************************************************************************************************************************

  input: any;
  applyFilter(event: Event) {
    const filterValue = (event.target as HTMLInputElement).value;
    this.input = filterValue;
    this.dataSource.filter = filterValue.trim().toLowerCase();
    if (this.dataSource.paginator) {
      this.dataSource.paginator.firstPage();
    }
  }

  refresh() {
    this.getDataByYearAndMonth();
  }

  ngOnDestroy(): void {
    this.destroy$.next(true);
    this.destroy$.complete();
  }

  //this.approver = "Hr Payroll Verifier";
  approver = "Senior Hr Payroll Verifier";
  isFinal = "Y";

  dataArray: any[] = [];
  getDataByYearAndMonth() {
    let params = new HttpParams()
      .set("periodY", this.formData.periodY.toString())
      .set("period_m", this.formData.periodM.toString())
      .set("status", "Approved")
      .set("is_final", this.isFinal)
      .set("approver", this.approver);
    this.payrollService
      .getPayrollsData(params)
      .pipe(takeUntil(this.destroy$))
      .subscribe({
        next: (res) => {
          console.log("res :", res);

          if (res.entity) {
            this.dataSource = new MatTableDataSource(res.entity);
            this.dataSource.paginator = this.paginator;
            this.dataSource.sort = this.sort;
          } else {
            this.snackbar.showNotification("snackbar-danger", res.message);
          }
        },
        error: (err) => {
          this.snackbar.showNotification("snackbar-danger", err.message);
        },
        complete: () => {},
      }),
      Subscription;
  }

  showPayrollDetails() {
    this.dataSource = new MatTableDataSource(
      this.formData.salariespaymentdetails
    );
    this.dataSource.paginator = this.paginator;
    this.dataSource.sort = this.sort;
  }

  //********************************************************************************** */
  showRecordDetails(row) {
    // Generate the HTML content for the SweetAlert modal
    const content = `
    <div style="font-family: Arial, sans-serif; text-align: left;">
      <table style="width: 100%;">
        <tr>
          <td><strong>Payroll Circle:</strong></td>
          <td>${row.payrollCircle}</td>
        </tr>
        <tr>
          <td><strong>Period:</strong></td>
          <td>${row.periodM} ${row.periodY}</td>
        </tr>
        <tr>
          <td><strong>Employee Name:</strong></td>
          <td>${row.employeeName}</td>
        </tr>
        <tr>
          <td><strong>Employee AC:</strong></td>
          <td>${row.employeeAc}</td>
        </tr>
        <tr>
          <td><strong>Employee ID No:</strong></td>
          <td>${row.empIdno}</td>
        </tr>
        <tr>
          <td><strong>KRA PIN:</strong></td>
          <td>${row.kraPin}</td>
        </tr>
        <tr>
          <td><strong>NHIF No:</strong></td>
          <td>${row.nhifNo}</td>
        </tr>
        <tr>
          <td><strong>NSSF No:</strong></td>
          <td>${row.nssfNo}</td>
        </tr>
        <tr>
          <td><strong>Employee Gross Pay:</strong></td>
          <td>${row.empGrosspay}</td>
        </tr>
        <tr>
          <td><strong>Employee PAYE:</strong></td>
          <td>${row.empPaye}</td>
        </tr>
        <tr>
          <td><strong>Employee NHIF:</strong></td>
          <td>${row.empNhif}</td>
        </tr>
        <tr>
          <td><strong>Employee NSSF:</strong></td>
          <td>${row.nssfEmployee}</td>
        </tr>
        <tr>
          <td><strong>Employee House Levy:</strong></td>
          <td>${row.employeeHouseLevy}</td>
        </tr>
        <tr>
          <td><strong>Employee Pension:</strong></td>
          <td>${row.pensionEmployee}</td>
        </tr>
        <tr>
          <td><strong>Employee Gratuity:</strong></td>
          <td>${row.empGratuity}</td>
        </tr>
        <tr>
          <td><strong>Employee Staff Loan:</strong></td>
          <td>${row.empStaffloan}</td>
        </tr>
        <tr>
          <td><strong>Employee Net Pay:</strong></td>
          <td>${row.empNetpay}</td>
        </tr>
      </table>
    </div>
  `;

    // Display the SweetAlert modal with custom styling
    Swal.fire({
      title: "Record Details",
      html: content,
      icon: "info",
      confirmButtonText: "Close",
    });
  }

  //********************************************************************************************************************** */
  //APPROVALS
  //********************************************************************************************************************** */

  loading: boolean = false;
  pushPayrollToFinance() {
    if (this.dataSource.data.length > 0) {
      Swal.fire({
        title: "Are you sure?",
        text: `Do you want to send this payroll for this period? Year: ${this.formData.periodY}, Month: ${this.formData.periodM}`,
        icon: "warning",
        showCancelButton: true,
        confirmButtonText: "Yes, send it!",
        cancelButtonText: "No, cancel!",
      }).then((result) => {
        if (result.isConfirmed) {
          this.loading = true;
          let params = new HttpParams()
            .set("periodY", this.formData.periodY.toString())
            .set("periodM", this.formData.periodM.toString());
          this.payrollService.sentToFinance(params).subscribe(
            (res) => {
              console.log("RES", res);
              this.loading = false;
              if (res.statusCode === 200) {
                Swal.fire({
                  icon: "success",
                  title: "Success!",
                  text: res.message,
                });
                this.router.navigate(["/erp-hr/payroll/to-finance-payroll"]);
              } else {
                Swal.fire({
                  icon: "warning",
                  title: "Warning!",
                  text: res.message,
                });
              }
            },
            (error) => {
              console.error("Error sending payroll:", error);
              Swal.fire({
                icon: "error",
                title: "Failed!",
                text: "Failed to send payroll: " + error.message,
              });
              this.loading = false;
            }
          );
        }
      });
    } else {
      this.snackbar.showNotification(
        "snackbar-danger",
        "No data to push for this Payroll!!"
      );
    }
  }
}
