import { SelectionModel } from '@angular/cdk/collections';
import { HttpParams } from '@angular/common/http';
import { Component, OnInit, ViewChild } from '@angular/core';
import { FormGroup, FormBuilder, Validators } from '@angular/forms';
import { MatDialog, MatDialogConfig } from '@angular/material/dialog';
import { MatPaginator } from '@angular/material/paginator';
import { MatSort } from '@angular/material/sort';
import { MatTableDataSource } from '@angular/material/table';
import { Router } from '@angular/router';
import { Subject, takeUntil, Subscription } from 'rxjs';
import { MonthsService } from 'src/@core/Models/months/months.service';
import { NotificationServiceService } from 'src/@core/Models/Notification/notification-service.service';
import { CustomAllowanceService } from 'src/app/erp-hr/data/payroll-services/custom-allowance.service';
import { PayrollCircleService } from 'src/app/erp-hr/data/payroll-services/payroll-circle.service';
import { SnackbarService } from 'src/app/shared/services/snackbar.service';
import Swal from 'sweetalert2';
import { ManagePayrollAllowancesComponent } from '../allowance-payroll/manage-payroll-allowances/manage-payroll-allowances.component';

@Component({
  selector: 'app-open-payroll',
  templateUrl: './open-payroll.component.html',
  styleUrls: ['./open-payroll.component.sass']
})
export class OpenPayrollComponent implements OnInit {

  displayedColumns: string[] = [
    // "select",
    "payrollCircleCode",
    "periodY",
    "periodM",
    "openingDate",

    "activeEmployees",
    "totalOpeningGrossPay",
    "totalOpeningDeductions",
    "totalOpeningAllowances",

    "openBy",
    "status",
    // "verifiedFlag",
    // "deletedFlag",
    // "action",
  ];

  loading: boolean = false;
  data: any;
  error: any;
  dialogConfig: any;
  destroy$: Subject<boolean> = new Subject<boolean>();

  dataSource!: MatTableDataSource<any>;
  @ViewChild(MatPaginator) paginator!: MatPaginator;
  @ViewChild(MatSort) sort!: MatSort;

  selection = new SelectionModel<any>(true, []);

  downloadLoading: boolean = false;
  posting: boolean = false;
  showForm: boolean = false;

  Form: FormGroup
  months: string[] = [
    'January', 'February', 'March', 'April', 'May', 'June',
    'July', 'August', 'September', 'October', 'November', 'December'
  ];
  constructor(
    private dialog: MatDialog,
    private notificationAPI: NotificationServiceService,
    private payrollCircleAPi: PayrollCircleService,
    private snackbar: SnackbarService,
    private fb: FormBuilder
  ) { }

  ngOnInit(): void {

    const currentYear = new Date().getFullYear();
    const currentMonthIndex = new Date().getMonth();
    const currentMonthName = this.months[currentMonthIndex];

    this.Form = this.fb.group({
      year: [currentYear.toString(), Validators.required],
      month: [currentMonthName, Validators.required],
    });
    this.Form.valueChanges.subscribe(() => {
      this.getData();
    });

    this.getData();
  }

  ngOnDestroy(): void {
    this.destroy$.next(true);
    this.destroy$.complete();
  }

  getData() {
    this.loading = true;
    this.dataSource = new MatTableDataSource([]);
    this.dataSource.paginator = this.paginator;
    this.dataSource.sort = this.sort;

    let params = new HttpParams()
      .set("month", this.Form.value.month)
      .set("year", this.Form.value.year);

    this.payrollCircleAPi
      .findAll()
      .pipe(takeUntil(this.destroy$))
      .subscribe({
        next: (res) => {
          if (res.statusCode === 200) {
            this.data = res.entity;
            console.log("Allowances displayed on table", this.data);

            this.loading = false;
            this.dataSource = new MatTableDataSource(this.data);
            this.dataSource.paginator = this.paginator;
            this.dataSource.sort = this.sort;
          } else {
            this.loading = false;
          }
        },
        error: (err) => {
          this.loading = false;
          this.notificationAPI.alertWarning(
            "Server Error: " + err.message
          );
        },
        complete: () => { },
      });
  }
  refresh() {
    this.getData();
  }

  mngRecord(action: any, record?: any) {
    console.log("this.action:; ", action);
    console.log("this.record:; ", record);

    const dialogConfig = new MatDialogConfig();
    dialogConfig.disableClose = false;
    dialogConfig.disableClose = true;
    dialogConfig.width = "800px";
    dialogConfig.data = {
      action: action,
      record: record,
    };
    const dialogRef = this.dialog.open(ManagePayrollAllowancesComponent, dialogConfig);
    dialogRef.afterClosed().subscribe((result) => {
      console.log("result:::", result);

      this.getData();
    });
  }


  input: any;
  applyFilter(event: Event) {
    const filterValue = (event.target as HTMLInputElement).value;
    this.input = filterValue;
    this.dataSource.filter = filterValue.trim().toLowerCase();
    if (this.dataSource.paginator) {
      this.dataSource.paginator.firstPage();
    }
  }

  // ****************************************************************************************************************************
  selectedRows: any[] = [];
  atleastOneSelected: boolean = false;
  isAllSelected() {
    const numSelected = this.selection.selected.length;
    const numRows = this.dataSource.data.length;
    return numSelected === numRows;
  }
  masterToggle() {
    if (this.isAllSelected()) {
      this.selection.clear();
      return;
    }

    this.selection.select(...this.dataSource.data);
  }
  checkboxLabel(row?: any): string {
    if (!row) {
      return `${this.isAllSelected() ? "deselect" : "select"} all`;
    }
    return `${this.selection.isSelected(row) ? "deselect" : "select"} row ${row.position + 1
      }`;
  }

  expSelected() {
    this.atleastOneSelected = true;
    this.selectedRows = this.selection.selected;

    console.log("this.selectedRows: ", this.selectedRows);
  }

  hasAccess: boolean = true;
  processMultipleRows(status: any) {
    let privilege = "Validate Expenses";

    // this.hasAccess = this.accessControlService.hasPrivilege("FinanceModule", [
    //   privilege,
    // ]);

    if (this.hasAccess) {
      Swal.fire({
    title: '<span style="font-size: 20px;">Enter Verifier Remarks</span>',
    input: "textarea",
    inputPlaceholder: "Type your remarks here...",
    confirmButtonText: "Submit",
    showCancelButton: true,
    cancelButtonText: "Cancel",
    icon: "info",

    inputValidator: (value) => {
          if (!value) {
            return "Please enter your remarks";
          }
        },
      }).then((result) => {
        if (result.isConfirmed) {
          const verifierRemarks = result.value;

          const processedRows = this.selectedRows.map((element) => {
            return {
              id: element.id,
              status: status,
              verifierRemarks: verifierRemarks,
            };
          });

          console.log("LOG: ", processedRows);

          // this.payrollCircleAPi
          //   .validateBulkAllowances(processedRows)
          //   .pipe(takeUntil(this.destroy$))
          //   .subscribe({
          //     next: (res) => {
          //       console.log("res: ", res);
          //       if (res.statusCode == 201 || 200 || 302) {
          //         this.snackbar.showNotification(
          //           "snackbar-success",
          //           res.message
          //         );

          //         this.selection = new SelectionModel<any>(true, []);
          //         this.selection.clear();
          //       } else {
          //         this.snackbar.showNotification(
          //           "snackbar-danger",
          //           res.message
          //         );
          //       }
          //     },
          //     error: (err) => {
          //       this.snackbar.showNotification("snackbar-danger", err.message);
          //     },
          //     complete: () => {
          //       this.getData();
          //     },
          //   }),
          //   Subscription;
        } else {
          this.selection = new SelectionModel<any>(true, []);
          this.selection.clear();
        }
      });
    }
  }

  onSubmit() {
    this.loading = true;
    let param = new HttpParams()
      .set('periodM', this.Form.value.month)
      .set('periodY', this.Form.value.year)
      .set('remarks', 'Init Payroll');
    this.payrollCircleAPi.open(param)
      .pipe(takeUntil(this.destroy$))
      .subscribe({
        next: (res) => {
          console.log("res: ", res);
          if (res.statusCode == 201) {
            this.snackbar.showNotification(
              "snackbar-success",
              res.message
            );
          } else {
            this.snackbar.showNotification(
              "snackbar-danger",
              res.message
            );
          }
        },
        error: (err) => {
          this.snackbar.showNotification("snackbar-danger", err.message);
          this.loading = false;
        },
        complete: () => {
          this.loading = false;
          this.getData()
        },
      }),
      Subscription;
  }
}


