  ngOnInit(): void {
    const currentYear = new Date().getFullYear();
    const currentMonthIndex = new Date().getMonth();
    const currentMonthName = this.months[currentMonthIndex];

    this.Form = this.fb.group({
      year: [currentYear.toString(), Validators.required],
      month: [currentMonthName, Validators.required],
    });
    this.Form.get('year').valueChanges.subscribe(() => {
      this.getData();
    });
    this.getData();
  }

  ngOnDestroy(): void {
    this.destroy$.next(true);
    this.destroy$.complete();
  }

  getData() {
    this.loading = true;
    this.dataSource = new MatTableDataSource([]);
    this.dataSource.paginator = this.paginator;
    this.dataSource.sort = this.sort;

    let params = new HttpParams()
      .set("periodY", this.Form.value.year);

    this.payrollAPi
      .getFinanceSalaryStatus(params)
      .pipe(takeUntil(this.destroy$))
      .subscribe({
        next: (res) => {
          console.log("res", res);

          if (res.statusCode === 302) {
            this.data = res.entity;
            console.log("Allowances displayed on table", this.data);

            this.loading = false;
            this.dataSource = new MatTableDataSource(this.data);
            this.dataSource.paginator = this.paginator;
            this.dataSource.sort = this.sort;
          } else {
            this.loading = false;
          }
        },
        error: (err) => {
          this.loading = false;
          this.notificationAPI.alertWarning("Server Error: " + err.message);
        },
        complete: () => {},
      });
  }
  refresh() {
    this.getData();
  }

  mngRecord(action: any, record?: any) {
    let route = "/erp-hr/payroll/manage-to-finance-payroll";

    let recordStr = {};
    if (action === "Push Payroll To Finance") {
      recordStr = {
        periodY: this.Form.value.year,
        periodM: this.Form.value.month,
      };
      recordStr = JSON.stringify(recordStr);
    } else {
      recordStr = JSON.stringify(record);
    }

    this.router.navigate([route], {
      queryParams: {
        action: action,
        record: recordStr,
      },
    });
  }

