import { SelectionModel } from '@angular/cdk/collections';
import { Component, OnInit, ViewChild } from '@angular/core';
import { FormGroup, FormBuilder, Validators } from '@angular/forms';
import { MatDialog, MatDialogConfig } from '@angular/material/dialog';
import { MatPaginator } from '@angular/material/paginator';
import { MatSort } from '@angular/material/sort';
import { MatTableDataSource } from '@angular/material/table';
import { Subject, takeUntil, Subscription } from 'rxjs';
import { NotificationServiceService } from 'src/@core/Models/Notification/notification-service.service';
import { BranchService } from 'src/app/erp-hr/data/configuration-services/branch.service';
import { SnackbarService } from 'src/app/shared/services/snackbar.service';
import Swal from 'sweetalert2';
import { ManageBranchesComponent } from '../../../configurations/branches/dialogs/manage-branches/manage-branches.component';
import { ActivatedRoute, Router } from '@angular/router';
import { HttpParams } from '@angular/common/http';
import { PayrollAnalysisService } from 'src/app/erp-hr/data/payroll-services/payroll-analysis.service';
import { PayrollCircleService } from 'src/app/erp-hr/data/payroll-services/payroll-circle.service';

@Component({
  selector: 'app-manage-close-payroll',
  templateUrl: './manage-close-payroll.component.html',
  styleUrls: ['./manage-close-payroll.component.scss']
})
export class ManageClosePayrollComponent implements OnInit {

  displayedColumns: string[] = [
    "category",
    "prevPayroll",
    "currPayroll",
    "changeRate"
  ];

  loading: boolean = false;
  data: any;
  error: any;
  dialogConfig: any;
  destroy$: Subject<boolean> = new Subject<boolean>();

  dataSource!: MatTableDataSource<any>;
  @ViewChild(MatPaginator) paginator!: MatPaginator;
  @ViewChild(MatSort) sort!: MatSort;

  selection = new SelectionModel<any>(true, []);

  downloadLoading: boolean = false;
  posting: boolean = false;
  showForm: boolean = false;

  Form: FormGroup
  mngForm: FormGroup

  months: string[] = [
    'JANUARY', 'FEBRUARY', 'MARCH', 'APRIL', 'MAY', 'JUNE',
    'JULY', 'AUGUST', 'SEPTEMBER', 'OCTOBER', 'NOVEMBER', 'DECEMBER'
  ];

  constructor(
    private dialog: MatDialog,
    private notificationAPI: NotificationServiceService,
    private branchAPI: BranchService,
    private snackbar: SnackbarService,
    private fb: FormBuilder,
    private route: ActivatedRoute,
    private payrollAnalysisAPI: PayrollAnalysisService,
    private payrollCircleAPi: PayrollCircleService,
    private router: Router,



  ) { }

  ngOnInit(): void {


    this.route.queryParams.subscribe((params) => {
      console.log("res", params);

      if (params.hasOwnProperty("year")) {
        this.currPeriodY = params["year"]
        this.currPeriodM = params["month"]
        this.payrollCircleCode = params["payrollCircleCode"]
      }
    });

    this.generateForm();
    this.initDates()

    this.Form = this.fb.group({
      year: [this.currPeriodY, Validators.required],
      month: [this.currPeriodM, Validators.required],
    });
    this.Form.valueChanges.subscribe(() => {
      this.getData();
    });

    this.getData();
  }

  ngOnDestroy(): void {
    this.destroy$.next(true);
    this.destroy$.complete();
  }


  generateForm(formData?): void {
    console.log("generateForm formData :: ", formData);
    this.mngForm = this.fb.group({
      remarks: [formData?.remarks ?? '', Validators.required],
      validsignature: [formData?.validsignature ?? '', Validators.required],
      
    });
  }
  // remarks:['', Validators.required],
  //     validsignature:['', Validators.required]
  payrollCircleCode: any

  currPeriodY: any
  currPeriodM: any
  currIsFinal: any
  prevMonthINT: any
  prevIsFinal: any
  prevPeriodM: any
  prevPeriodY: any
  initDates() {

    this.currIsFinal = 'N'
    this.prevMonthINT = (new Date().getMonth() === 0) ? 11 : new Date().getMonth() - 1;
    this.prevPeriodM = new Date(Date.UTC(0, this.prevMonthINT)).toLocaleString('en-US', { month: 'long' }).toUpperCase();
    this.prevIsFinal = 'Y'
    if (this.prevPeriodM == "DECEMBER") {
      this.prevPeriodY = this.currPeriodY - 1;
    } else {
      this.prevPeriodY = this.currPeriodY;
    }
  }
  //****************************************************************************************** */


  getData() {
    this.loading = true;
    this.dataSource = new MatTableDataSource([]);
    this.dataSource.paginator = this.paginator;
    this.dataSource.sort = this.sort;

    let param = new HttpParams()
      .set('prevPeriodY', this.prevPeriodY)
      .set('prevPeriodM', this.prevPeriodM)
      .set('prevIsFinal', this.prevIsFinal)
      .set('currPeriodY', this.currPeriodY)
      .set('currPeriodM', this.currPeriodM)
      .set('currIsFinal', this.currIsFinal);
    console.log(param)
    this.payrollAnalysisAPI.findAll(param)
      .pipe(takeUntil(this.destroy$))
      .subscribe({
        next: (res) => {

          console.log('RES:: ', res)

          if (res.statusCode === 200) {
            this.data = res.entity;
            console.log("Data displayed on table", this.data);

            this.loading = false;
            this.dataSource = new MatTableDataSource(this.data);
            this.dataSource.paginator = this.paginator;
            this.dataSource.sort = this.sort;
          } else {
            this.loading = false;

            this.notificationAPI.alertWarning(
              res.message
            );
          }
        },
        error: (err) => {
          this.loading = false;
          this.notificationAPI.alertWarning(
            "Server Error: " + err.message
          );
        },
        complete: () => { },
      });
  }
  refresh() {
    this.getData();
  }


  input: any;
  applyFilter(event: Event) {
    const filterValue = (event.target as HTMLInputElement).value;
    this.input = filterValue;
    this.dataSource.filter = filterValue.trim().toLowerCase();
    if (this.dataSource.paginator) {
      this.dataSource.paginator.firstPage();
    }
  }


  onSubmit() {
    this.loading = true;
  
    // Using SweetAlert to confirm the action
    Swal.fire({
      title: 'Confirm',
      text: 'Are you sure you want to close payroll for this period?',
      icon: 'warning',
      showCancelButton: true,
      confirmButtonColor: '#3085d6',
      cancelButtonColor: '#d33',
      confirmButtonText: 'Yes, close it!'
    }).then((result) => {
      if (result.isConfirmed) {
        if (this.mngForm.valid) {
          if (this.mngForm.controls.validsignature.value == 'FINAL ENTRY') {
            let param = new HttpParams()
              .set('payrollCircleCode', this.payrollCircleCode)
              .set('closingRemarks', this.mngForm.controls.remarks.value);
  
            this.payrollCircleAPi.close(param).subscribe(res => {
              this.loading = false;
              if (res.statusCode == 200) {
                this.loading = false;
                Swal.fire({
                  icon: 'success',
                  title: 'Success!',
                  text: res.message
                }).then((result) => {
                  if (result.isConfirmed || result.isDismissed) {
                    this.router.navigate(['/erp-hr/payroll/close-payroll'] );
                  }
                });
              } else {
                this.loading = false;
                Swal.fire({
                  icon: 'error',
                  title: 'Oops...',
                  text: res.message
                });
              }
            });
          } else {
            this.loading = false;
            Swal.fire({
              icon: 'warning',
              title: 'Warning',
              text: 'YOU MUST ENTER THE CORRECT APPROVAL SIGNATURE'
            });
          }
        } else {
          this.loading = false;
          Swal.fire({
            icon: 'warning',
            title: 'Warning',
            text: 'INVALID FORM DATA'
          });
        }
      } else {
        this.loading = false;
        this.getData();
      }
    });
  }
  onNoClick(){
    this.router.navigate(['/erp-hr/payroll/close-payroll'] );
  }
  // ****************************************************************************************************************************

}
